.project {
	overflow: hidden;
	&--items {
		display: flex;
		flex-direction: column;

		margin-top: 170px;
		margin-bottom: 100px;
	}
	&--item {
		display: flex;
		align-items: center;

		margin-bottom: 210px;
		&__img {
			width: 630px;
			flex-shrink: 0;
			position: relative;
			img {
				width: 100%;
				height: auto;
				z-index: 2;
			}
		}
		&__img::before {
			content: "";

			display: block;
			width: 630px;
			height: 470px;

			position: absolute;
			top: -70px;
			left: -370px;

			background-color: #55b7ff;

			z-index: -1;
		}
		&__desc {
			margin-left: 75px;
			h5 {
				color: #2c4058;
				font-size: 2.8rem;
				line-height: 24px;
				text-transform: uppercase;
				margin-bottom: 40px;
				span {
					display: block;
					margin-bottom: 7px;
					font-size: 1.4rem;
					font-weight: 400;
				}
			}
			p {
				color: #4b5562;
				font-family: "Open Sans";
				font-size: 1.2rem;
				line-height: 30px;
				text-align: left;
				margin-bottom: 35px;
			}
			.btn {
			}
			.adress {
				display: block;

				margin-top: 40px;

				color: #2c4058;
				font-family: "Open Sans";

				font-size: 1.2rem;
				font-style: italic;

				text-align: left;
			}
		}
	}
	&--item:last-child {
		margin-bottom: 140px;
	}
	&--item:nth-child(2n) {
		flex-direction: row-reverse;
	}
	&--item:nth-child(2n) .project--item__img::before {
		left: auto;
		right: -370px;
		background-color: #7e5aff;
	}
	&--item:nth-child(2n) .project--item__desc {
		margin-right: 75px;
		margin-left: 0px;
	}
}