.contact {
	height: 700px;
	background-image: url(../img/contact-bg.jpg);
	background-size: cover;
	position: relative;
	&--form {
		position: absolute;
		top: 400px;
		left: 50%;
		transform: translateX(-50%);
		width: 820px;
		margin: 0 auto;
		background-color: #fff;
		padding: 100px 80px;
		z-index: 100;
		&__wrap {
			display: flex;
			margin-top: 60px;
			& > div {
				width: 50%;
				height: 100%;
			}
		}
		&__info {
			padding-right: 25px;
			display: flex;
			flex-direction: column;
			justify-content: space-between;
			& > * {
				display: block;
				color: #2c4058;
				font-size: 1.1rem;
				font-weight: 400;
				margin-bottom: 25px;
				i {
					margin-right: 5px;
					color: #7e5aff;
				}
			}
			& > *:last-child {
				margin-bottom: 0;
			}
		}
		&__input {
			padding-left: 25px;
			input {
				display: block;
				width: 100%;
				padding-bottom: 10px;
				margin-bottom: 15px;

				font-size: 1.1rem;
				border: none;
				border-bottom: 1px solid #2c4058;

				outline: none;
			}
			.input__btn {
				margin-top: 50px;
				text-align: right;
				.btn {
					padding: 15px 30px;
				}
			}
		}
	}
}