.intro { 
	height: 100vh;
	background-color: #55b7ff;
	background-image: linear-gradient(45deg, #55b7ff 0%, #7e5aff 100%);

	position: relative;

	&--inner {
		display: flex;
		flex-direction: column;
		justify-content: space-between;

		height: 100vh;

		padding: 50px 0;
	}
	&--text {
		display: flex;
		flex-direction: column;
		flex-grow: 1;

		justify-content: center;
		align-items: center;

		color: #ffffff;

		h1 {
			font-size: 5.35rem;
			font-weight: 700;

			text-transform: uppercase;
			letter-spacing: 4px;
		}
		p {
			font-size: 1.2rem;
			font-weight: 400;

			text-transform: uppercase;
			letter-spacing: 5.4px;
		}
	}
	&--down {
		display: flex;
		justify-content: center;
		.btn-icon {
			i {
				animation-duration: 3s;
				animation-name: down;
				animation-iteration-count: infinite;
			}
		}
	}
}
.intro::after {
	content: "";
	display: block;

	position: absolute;
	left: 0;
	top: 0;
	right: 0;
	bottom: 0;

	height: 100%;

	background-image: url(../img/intro-image-bg.png);
	background-size: cover;

	pointer-events: none;
}