.news {
	background-image: linear-gradient(45deg, #3c7fd4 0%, #24ccfa 100%);
	padding-bottom: 160px;
	color: #fff;
	.section--heading h3{
		color: #fff;
	}
	.section--heading h3::before {
		background-color: #fff;
	}
	.news--slider {
		margin-top: 100px;

		&__item {
			outline: none;
			padding: 0 10px;
			transition: opacity .3s;
			opacity: 0.95;
			.img img {
				width: 100%;
			}
			.content {
				color: #000;
				padding: 30px 20px;
				background-color: #fff;
				.desc{

					h4 a{
						color: #2c4058;
						font-size: 1.4rem;
						text-transform: uppercase;
					}
					p {
						color: #4b5562;
						font-family: "Open Sans";
						font-size: 1.1rem;
						font-weight: 400;
						margin: 20px 0 40px;
					}
				}
				.author {
					display: flex;
					img {
						border-radius: 50%;
					}
					div {
						margin-left: 20px;
						h5 {
							margin-bottom: 10px;
						}
						h5 a{
							color: #2c4058;
							font-size: 1.1rem;
							font-weight: 700;
							text-transform: uppercase;
						}
						time a{
							color: #55b7ff;
							font-size: 1rem;
							font-weight: 400;
						}
					}
				}
			}

		}
		&__item:hover {
			opacity: 1;
		}
	}
}