header {
	z-index: 1; 

	display: flex;
	justify-content: space-between;
	align-items: center;

	.header--nav {
		ul{
			display: flex;

			li {
				a {
					display: inline-block;
					margin-left: 60px;

					color: #ffffff;

					font-weight: 400;

					text-transform: uppercase;

					position: relative;
				}

				a::first-child {
					margin-left: 0;
				}

				a::before {
					content: "";

					display: block;
					width: 9px;
					height: 9px;

					border: 1px solid #fff;
					border-radius: 50%;

					background-color: transparent;
					position: absolute;
					left: -15px;
					top: 50%;
					transform: translateY(-50%); 

					transition: background-color .2s ;
				}

				a.active::before, a:hover::before {
					background-color: #fff;
				}
			}
		}
	}
	.header--social {
		display: flex;
		
		.btn-icon {
			margin: 0 5px;
		}
	}
}