.footer {
	height: 530px;
	padding-top: 220px;
	background-image: linear-gradient(45deg, #55b7ff 0%, #7e5aff 100%);

	position: relative;

	&--inner {
		display: flex;
		justify-content: space-between;

	}
	&--social {
		display: flex;
		
		.btn-icon {
			margin: 0 5px;
		}
	}
	&--text {
		margin-top: 40px;
	}
	p {
		color: #fff;
		margin-right: 40px;
		position: relative;
		display: inline-block;
	}
	p::after {
		content: "";
		display: block;
		position: absolute;
		right: -20px;top: 0;
		width: 1px;
		height: 100%;
		background-color: #fff;
	}
	span {
		color: #fff;
	}
}
.footer::before {
	content: "";

	position: absolute;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;

	background-image: url(../img/footer-bg.png);
	background-size: cover;
	background-position: center center;

	pointer-events: none;
}