.galery {
	padding-bottom: 140px;
	&--box {
		margin-top: 100px;
		margin-bottom: 60px;
		display: grid;
		grid-template-columns: 2fr 1fr 1fr;
		grid-gap: 10px;

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
		&__item {
			position: relative;
		}
		
		&__item:first-child {
			grid-row: 1 / 3;
		}

		&__item::before {
			content: " ";

			display: block;

			position: absolute;
			top: 0;
			right: 0;
			bottom: 0;
			left: 0;

			background: rgba(0,0,0,.3);

			cursor: pointer;


			z-index: 10;
			transition: opacity .3s;
			opacity: 0;
		}

		&__item::after {
			content: " ";

			display: block;
			width: 80px;
			height: 80px;

			position: absolute;
			top: 0;
			right: 0;
			bottom: 0;
			left: 0;

			background-image: url(../img/galery-hover.png);
			cursor: pointer;

			left: 50%;
			top: 50%;
			transform: translate(-50%, -50%);

			z-index: 102;
			transition: opacity .3s;
			opacity: 0;
		}

		&__item:hover::before, &__item:hover::after {
			opacity: 1;
		}
	}
	&--btn {
		text-align: center;
	}
}