*, 
*::before,
*::after {
    box-sizing: border-box;
}

html, body {
    margin: 0;
    padding: 0;
}

html {
    font-size: 15px;
}

body {
    font-size: 1rem;
    font-family: "Montserat", "Arial", sans-serif;
}

h1, h2, h3, h4, h5, h6 {
    padding: 0;
    margin: 0;
}

a {
    text-decoration: none;
}

ul {
    margin: 0;
    padding: 0;

    list-style: none;
}

img {
    max-width: 100%;
}

.container {
    width: 1170px; max-width: 100%;
    padding: 0 15px;
    margin: 0 auto;
}

.btn {    
    padding: 20px 40px;

    color: #2c4058;
    font-size: 1rem;

    text-transform: uppercase;

    border: 1px solid #2c4058;

    transition: background-color .3s, color .3s, box-shadow .3s;
}
.btn:hover {
    background-color: #2c4058;
    color: #fff; 
    box-shadow: 1px 1px 2px #2c4058;
}

.btn-icon {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 40px;
    height: 40px;
    align-items: center;
    justify-content: center;
    border: 3px solid #fff;
    border-radius: 50%;
    color: #fff;

    transition: box-shadow .3s;
}
.btn-icon:hover, .btn-icon:hover i{
    border-color: #fff;
    box-shadow: 0 0 15px #fff;
}

section {
    padding-top: 140px;
}

.section--heading {
    display: flex;
    justify-content: center;
    h3 {
        color: #2c4058;
        font-size: 3rem;
        font-weight: 700;
        text-transform: uppercase;

        position: relative;
    }
    h3::before {
        content: "";

        display: block;
        width: 100px;
        height: 2px;

        background-color: #2c4058;

        position: absolute;
        top: -2rem;
        left: 50%;
        transform: translateX(-50%);

        z-index: 5;
    }
}